body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  font-size: 62.5%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}

.app-container {
  background-image: -webkit-gradient(
    linear,
    left top, right bottom,
    from(rgba(17, 198, 104, 0.502)),
    color-stop(70%, rgba(55, 104, 6, 0.427))
  );
  background-image: -webkit-linear-gradient(
    left top,
    rgba(17, 198, 104, 0.502),
    rgba(55, 104, 6, 0.427) 70%
  );
  background-image: -o-linear-gradient(
    left top,
    rgba(17, 198, 104, 0.502),
    rgba(55, 104, 6, 0.427) 70%
  );
  background-image: linear-gradient(
    to right bottom,
    rgba(17, 198, 104, 0.502),
    rgba(55, 104, 6, 0.427) 70%
  );
  max-width: 800px;
  min-height: 70vh;
  margin: auto;
  margin-top: 4rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: start;
      justify-content: flex-start;
  border-radius: 3px;
  padding: 3rem;
  -webkit-box-shadow: 0 0.3rem 4rem 1rem rgba(1, 1, 1, 0.3);
          box-shadow: 0 0.3rem 4rem 1rem rgba(1, 1, 1, 0.3);
}

.app-heading {
  color: white;
  margin-bottom: 2rem;
  text-transform: uppercase;
  font-size: 3rem;
  text-shadow: 0 0.5rem 2rem rgba(1, 1, 1, 0.3);
}

.todo-form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  width: 80%;
  /* background: red; */
}

.todo-input {
  height: 2rem;
  font-size: 1.3rem;
  text-align: center;
  margin-bottom: 0.5rem;
  outline: none;
  border: none;
  border-radius: 3px;
  -ms-flex: 1 1;
      flex: 1 1;
}

.todo-btn {
  background-color: darkolivegreen;
  border: transparent;
  height: 2rem;
  border-radius: 3px;
  outline: none;
  font-size: 1.2rem;
  text-transform: uppercase;
  line-height: 1.4rem;
  color: white;
  margin-left: 0.4rem;
}

.todo-btn:hover {
  background-color: rgb(122, 154, 66);
  outline: none;
}

.todo-list__container {
  width: 80%;
}

.todo-item {
  width: 100%;
  margin-bottom: 0.3rem;
  opacity: 1;
  height: 3rem;
  font-size: 1.4rem;
  line-height: 3rem;
  color: aliceblue;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  border: 2px solid rgba(255, 255, 255, 1);
  border-radius: 3px;
  -webkit-transform-origin: top;
      -ms-transform-origin: top;
          transform-origin: top;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}

.fade-enter {
  opacity: 0.01;
}
.fade-enter-active {
  opacity: 1;
  -webkit-transition: opacity 300ms ease-in;
  -o-transition: opacity 300ms ease-in;
  transition: opacity 300ms ease-in;
}
.fade-exit {
  opacity: 1;
}
.fade-exit-active {
  opacity: 0.01;
  -webkit-transition: opacity 300ms ease-in;
  -o-transition: opacity 300ms ease-in;
  transition: opacity 300ms ease-in;
}

.todo-item__text {
  height: 100%;
  margin: 0;
  -ms-flex: 1 1;
      flex: 1 1;
  padding: 0 0.3rem;
}

.todo-item__done {
  height: 100%;
  font-size: 1.7rem;
  opacity: 0;
  -webkit-transform: scaleX(0) translateX(5px);
      -ms-transform: scaleX(0) translateX(5px);
          transform: scaleX(0) translateX(5px);
  width: 0;
  -webkit-transform-origin: right;
      -ms-transform-origin: right;
          transform-origin: right;
  background: green;
  padding: 0 0.4rem;
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  transition: all 0.4s;
  text-align: center;
}

.todo-item__done:hover {
  color: coral;
  cursor: pointer;
}

.todo-item:hover .todo-item__done {
  -webkit-transform: scaleX(1) translateX(0);
      -ms-transform: scaleX(1) translateX(0);
          transform: scaleX(1) translateX(0);
  opacity: 1;
  width: 3rem;
}

.todo-item__remove {
  height: 100%;
  font-size: 1.7rem;
  background: rgb(181, 70, 70);
  padding: 0 0.2rem;
  -webkit-transform: scaleX(0) translateX(-5px);
      -ms-transform: scaleX(0) translateX(-5px);
          transform: scaleX(0) translateX(-5px);
  -webkit-transform-origin: left;
      -ms-transform-origin: left;
          transform-origin: left;
  opacity: 0;
  width: 0;
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  transition: all 0.4s;
  text-align: center;
}

.todo-item__remove:hover {
  color: rgb(255, 235, 13);
  cursor: pointer;
}

.todo-item:hover .todo-item__remove {
  -webkit-transform: scaleX(1) translateX(0);
      -ms-transform: scaleX(1) translateX(0);
          transform: scaleX(1) translateX(0);
  opacity: 1;
  width: 3rem;
}

.completed-list {
  width: 80%;
  background: white;
  text-align: center;
  height: 2.5rem;
  line-height: 2.5rem;
  -webkit-transition: all 0.1s;
  -o-transition: all 0.1s;
  transition: all 0.1s;
  margin-bottom: 0.5rem;
  border-radius: 3px;
  -webkit-box-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.5);
          box-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.5);
}

.heading__secondary {
  font-size: 1.5rem;
  color: darkolivegreen;
  text-transform: uppercase;
  cursor: pointer;
}

.completed-list:hover {
  -webkit-transform: translateY(-0.1rem);
      -ms-transform: translateY(-0.1rem);
          transform: translateY(-0.1rem);
}

.completed-list:active {
  -webkit-transform: translateY(-0.05rem);
      -ms-transform: translateY(-0.05rem);
          transform: translateY(-0.05rem);
}

@-webkit-keyframes showTodo {
  0% {
    opacity: 0;
    height: 0;
  }
  100% {
    opacity: 1;
    height: 3rem;
  }
}

@keyframes showTodo {
  0% {
    opacity: 0;
    height: 0;
  }
  100% {
    opacity: 1;
    height: 3rem;
  }
}

@-webkit-keyframes hideTodo {
  0% {
    opacity: 1;
    height: 3rem;
  }
  100% {
    opacity: 0;
    height: 0;
  }
}

@keyframes hideTodo {
  0% {
    opacity: 1;
    height: 3rem;
  }
  100% {
    opacity: 0;
    height: 0;
  }
}

/* Example */

.example-enter {
  opacity: 0.01;
}

.example-enter.example-enter-active {
  opacity: 1;
  -webkit-transition: opacity 500ms ease-in;
  -o-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in;
}

.example-leave {
  opacity: 1;
}

.example-leave.example-leave-active {
  opacity: 0.01;
  -webkit-transition: opacity 300ms ease-in;
  -o-transition: opacity 300ms ease-in;
  transition: opacity 300ms ease-in;
}

.fade-enter {
  opacity: 0.01;
}

.fade-enter-active {
  opacity: 1;
  -webkit-transition: opacity 500ms ease-in;
  -o-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in;
}

.fade-exit {
  opacity: 1;
}

.fade-exit-active {
  opacity: 0.01;
  -webkit-transition: opacity 500ms ease-in;
  -o-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in;
}

.footer {
  margin-top: auto;
  color: white;
  font-size: 15px;
}



